.footer__contact {
  padding: 116px 0;
  display: flex;

  @include vp-1023 {
    padding-bottom: 77px;
  }

  @include vp-767 {
    flex-wrap: wrap;
    padding-top: 0;
    padding-bottom: 54px;
  }
}

.footer__map {
  flex: 1 0 50%;
  max-width: 560px;
  background-image: url("../img/content/map-bg.jpg");

  @include vp-767 {
    order: 2;
  }
}

.footer__map iframe {
  width: 560px;
  height: 306px;
  border: none;

  @include vp-1023 {
    width: 324px;
    height: 335px;
  }

  @include vp-767 {
    width: 100%;
    height: 316px;
  }
}

.footer__text {
  padding-top: 60px;
  padding-left: 99px;
  margin-bottom: 33px;

  @include vp-1023 {
    padding-top: 32px;
    padding-left: 15px;
  }

  @include vp-767 {
    margin-bottom: 33px;
    padding-top: 40px;
    padding-left: 0;
  }
}

.footer__text p {
  font-weight: 500;
  font-size: 18px;
  line-height: 150%;
  color: $color-tangaroa;
  margin-bottom: 33px;

  @include vp-1023 {
    margin-bottom: 30px;
  }
}

.footer__text address {
  display: flex;
  font-style: normal;
  font-size: 16px;
  line-height: 150%;
  margin-bottom: 20px;
}

.footer__text svg {
  margin-right: 10px;

  @include vp-1023 {
    width: 25px;
    height: 22px;
  }
}

.footer__map-link {
  font-size: 16px;
  line-height: 150%;
  color: $color-tangaroa;
}
