form {
  box-sizing: border-box;
  max-width: 403px;
  padding: 30px 50px 50px;
  background-color: $color-tangaroa;
  box-shadow: 0 4px 45px rgba(1, 28, 64, 0.15);

  @include vp-767 {
    padding: 20px 30px 40px 29px;
  }
}

form p {
  font-weight: 500;
  font-size: 18px;
  line-height: 150%;
  color: $color-alice-blue;
  margin-top: 15px;
  margin-bottom: 26px;

  @include vp-767 {
    text-align: center;
    margin-bottom: 22px;
  }
}

.form__item {
  margin-bottom: 35px;
  position: relative;

  @include vp-767 {
    margin-bottom: 27px;
  }
}

.form__item span {
  display: block;
  font-size: 10px;
  line-height: 120%;
  color: $color-alice-blue;
  padding-left: 23px;
}

form input {
  display: block;
  width: 100%;
  font-size: 14px;
  line-height: 150%;
  color: $color-default-black;
  background-color: $color-alice-blue;
  padding: 11px 16px;
  border: none;
  margin-bottom: 20px;
}

.checkbox__custom::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 18px;
  height: 18px;
  background-color: $color-alice-blue;

  @include vp-767 {
    top: 2px;
  }
}

.checkbox input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.checkbox input:checked + .checkbox__custom::after {
  position: absolute;
  content: "";
  top: 2px;
  left: 4px;
  height: 10px;
  width: 10px;
  background-image: url("../img/svg/checked.svg");
  background-size: 18px 18px;
  background-repeat: no-repeat;
  background-position: center;

  @include vp-767 {
    top: 4px;
  }
}
