.intro {
  padding: 120px 0;
  background-color: $background;

  @include vp-1023 {
    padding: 90px 0 68px;
  }

  @include vp-767 {
    padding: 57px 0;
  }
}

.intro__wrapper {
  min-height: 301px;
  display: flex;
  justify-content: space-between;

  @include vp-767 {
    flex-wrap: wrap;
    justify-content: start;
  }
}

.intro__text {
  flex: 0 0 50%;
  align-items: center;
  padding-right: 30px;

  @include vp-767 {
    flex: 1 1 100%;
    padding-right: 0;
  }
}

.intro__text h2 {
  font-weight: 600;
  font-size: 28px;
  line-height: 150%;
  margin-top: 55px;
  margin-bottom: 26px;

  @include vp-1023 {
    margin-top: 0;
    font-size: 26px;
    line-height: 140%;
  }

  @include vp-767 {
    margin-bottom: 10px;
  }
}

.intro__text p {
  font-size: 16px;
  line-height: 150%;

  @include vp-767 {
    margin: 0;
    margin-bottom: 20px;
  }
}

.intro__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;

  @include vp-1023 {
    width: 324px;
    height: 264px;
    margin-top: 10px;
  }

  @include vp-767 {
    width: 100%;
    height: 192px;
  }
}
